.custom-btn {
  width: 100%;
  color: #ababab;
  background-color: #181818;
  border: none;
  border-radius: 9px;
  font-size: 1rem;
  font-weight: 700;
  padding: 3px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.custom-btn:hover {
  color: #9b9b9b;
  background-color: #111111;
}

.custom-btn:active {
  -webkit-transition: all 3ms ease-in-out;
  transition: all 3ms ease-in-out;
  color: #868686;
  background-color: #080808;
}

.custom-btn-clear {
  background-color: #3b3b3b;
}

.custom-btn-clear:hover {
  color: #9b9b9b;
  background-color: #111111;
}

.custom-btn-clear:active {
  -webkit-transition: all 3ms ease-in-out;
  transition: all 3ms ease-in-out;
  color: #868686;
  background-color: #080808;
}
